import { connect } from 'react-redux';
import RetailTransferHistoryComponent from './RetailTransferHistoryComponent';
import { transferReceiverUserNames } from '../../redux/selectors/transferSelectors';

const mapStateToProps = state => {
  const { fetching } = state.transfer;
  const transfers = transferReceiverUserNames(state);

  return { transfers, fetching };
};

export default connect(mapStateToProps)(RetailTransferHistoryComponent);



