import { createSelector } from 'reselect';
import { distinct } from '../../helpers/arrayHelper';

export const transferReceiverUserNames = createSelector(
  state => state.transfer.transfers,
  transfers => {
    return distinct(transfers.map(transfer => transfer.ReceiverUserName));
  }
);



