import React from 'react';
import PropTypes from 'prop-types';
import Spinner from '../common/Spinner/Spinner';
import APIcon from '../common/APIcon/APIcon';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './RetailTransferHistoryComponent.css';

const retailTransferHistoryClasses = getBEMClasses('retail-transfer-history');

const RetailTransferHistoryComponent = (props, context) => {
  const { transfers, fetching } = props;

  const renderContent = () => {
    if (fetching) {
      return (
        <div className={retailTransferHistoryClasses('spinner')}>
          <Spinner isInline />
        </div>
      );
    } else if (transfers.length === 0 && !fetching) {
      return (
        <div className={retailTransferHistoryClasses('item')}>
          <APIcon
            name="stop"
            customClass={retailTransferHistoryClasses('icon')}
          />
          {context.t('No Transfer History')}
        </div>
      );
    } else {
      return transfers.map((userName, index) => {
        return (
          <div key={index} className={retailTransferHistoryClasses('item')}>
            {userName}
          </div>
        );
      });
    }
  };

  return (
    <div className={retailTransferHistoryClasses()}>
      <div className={retailTransferHistoryClasses('header')}>
        {context.t('Transfer History')}
      </div>
      <div className={retailTransferHistoryClasses('items')}>
        {renderContent()}
      </div>
    </div>
  );
};

RetailTransferHistoryComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailTransferHistoryComponent;



