import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import RetailTransferHistoryContainer from './RetailTransferHistoryContainer';

import './RetailUserContactsComponent.css';

const retailUserContactsClasses = getBEMClasses('retail-user-contacts');

const RetailUserContactsComponent = (props, context) => {
  return (
    <div className={retailUserContactsClasses()}>
      <div className={retailUserContactsClasses('block-with-text')}>
        <div className={retailUserContactsClasses('header-text')}>
          {context.t('Trading with Other On-Exchange Participants')}
        </div>
        <div className={retailUserContactsClasses('text')}>
          {context.t(
            'You can freely transfer currency with other participants on the exchange. Simply provide the email address for your contact during the “Send” or “Receive” process. If the email address is registered on the exchange, then the user will receive the transfer. If the email address is not registered, the user will get an invitation to join the exchange.'
          )}
        </div>
      </div>
      <RetailTransferHistoryContainer />
    </div>
  );
};

RetailUserContactsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailUserContactsComponent;



